import React, { Component } from "react";
// TODO: import as styles
import "./editor.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrashAlt } from "@fortawesome/free-solid-svg-icons";

export class FileList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      graphList: sortGraphList(this.props.graphList),
    };
  }

  componentDidUpdate(prevProps) {
    if(prevProps.graphList !== this.props.graphList) {
      this.setState({
        ...this.state,
        graphList: this.props.graphList,
      });
    }
  }

  loadFile = name => {
    const { loadGraph } = this.props;
    loadGraph(name);
  }

  deleteFile = name => {
    const { deleteGraph } = this.props;
    deleteGraph(name);
  }

  render() {
    const { graphList } = this.state;
    return (
      <div className={`filenamesListContainer`}>
        {graphList.map(({name, readonly}, idx) =>
          <FileItem
            key={`EditorFileItem${idx}`}
            name={name}
            readonly={readonly}
            loadFile={this.loadFile}
            deleteFile={this.deleteFile}
          />
        )}
      </div>
    );
  }
}

class FileItem extends Component {
  load = () => {
    const { name, loadFile } = this.props;
    loadFile(name);
  }

  delete = () => {
    const { name, deleteFile } = this.props;
    deleteFile(name);
  }

  render() {
    const { name, readonly } = this.props;
    return (
      <div className={`filenameControlsContainer`}>
        <button
          title={`Load ${name}`}
          className={`filenameButton`}
          onClick={this.load}
        >
          <span className={`filenameButtonText`}>{name}</span>
        </button>
        <button
          title={readonly ? `You cannot delete a preset` : `Delete`}
          className={readonly ? `inactive` : `critical`}
          disabled={readonly}
          onClick={readonly ? undefined : this.delete}
        >
          <FontAwesomeIcon icon={faTrashAlt} />
        </button>
      </div>
    );
  }
}

function sortGraphList(graphList) {
  const readonly = graphList.filter(({readonly}) => readonly);
  const writeable = graphList.filter(({readonly}) => !readonly)
    .sort((a, b) => a.name.toLowerCase().localeCompare(b.name.toLowerCase()));
  return [...readonly, ...writeable];
}



