import React, { Component } from "react";
// TODO: import as styles
import "./editor.css";
import { VALID_GRAPH_NAME_PATTERN } from "../../utils/graph-store";
import { faClipboard } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export class FileBar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      originalFilename: this.props.filename,
      filename: this.props.filename,
      blockedFilenames: this.props.blockedFilenames,
      save: this.props.save,
    };
  }

  componentDidUpdate(prevProps) {
    if (prevProps.filename !== this.props.filename) {
      this.setState({
        ...this.state,
        originalFilename: this.props.filename,
        filename: this.props.filename,
        blockedFilenames: this.props.blockedFilenames,
        save: this.props.save,
      });
    }
  }

  updateFilename = e => {
    const cleanFilename = e.target.value.trim();
    if (e.target.value !== cleanFilename) e.target.value = cleanFilename;
    this.setState({
      ...this.state,
      ...{
        filename: cleanFilename,
      },
    })
  }

  save = () => {
    const { save, filename } = this.state;
    save(filename);
  }

  render() {
    const { filename, originalFilename, blockedFilenames } = this.state;
    let save = {
      text: "Save",
      title: "Overwrite",
      disabled: false,
    };
    if (!VALID_GRAPH_NAME_PATTERN.test(filename)) {
      save.title = "Invalid file name";
      save.disabled = true;
    } else {
      if (blockedFilenames.includes(filename)) {
        save.title = "Cannot overwrite";
        save.disabled = true;
      } else {
        if (originalFilename !== filename) {
          save.text = "Save as...";
          save.title = "Create new";
        }
      }
    }

    // TODO: Show graph exported code JSON
    // TODO: Make FileBar sticky
    return (
      <div className={`fileBar`}>
        <input
          type={"text"}
          defaultValue={filename}
          onKeyUp={this.updateFilename}
        />
        <button
          className={"fileSaveButton"}
          title={save.title}
          disabled={save.disabled}
          onClick={this.save}
        >
          {save.text}
        </button>
        <button
          disabled={true}
          title={"Open graph code JSON"}
        >
          <FontAwesomeIcon icon={faClipboard} />
        </button>
      </div>
    );
  }

}



